import { createSlice } from '@reduxjs/toolkit'
import Cookies from 'universal-cookie';

const cookies = new Cookies();

let initialLang = cookies.get('lang')
if (initialLang === undefined) {
  initialLang = 'en'
  cookies.set('lang', 'en', { path: '/' })
}

